<template lang="pug">
    div.user-list
        ul(v-if="list && list.length > 0") 
            li.list(v-for="(item, index) in list")
                div.head-img
                    router-link(:to="{path: '/user/others', query: {to_user_id : item.user_id}}")
                        img(v-lazy="item.head_image" width="20px")
                div.user-information
                    div.user-title
                        div.user-nick-name  
                            span(v-html="item.nick_name")
                            div.praise
                                i.icon.iconfont.sex-man.icon-sex(v-if="item.sex == 1") &#xe661;
                                i.icon.iconfont.sex-woman.icon-sex(v-else) &#xe662;
                                img.icon-level(:src="iconLevel[index]")
                        div.signature(v-html="item.signature")
                    div.after
                        div.btn-follow.followed(v-if="item.follow_id>0 || item.has_focus>0" @click="follow(item)")
                            i.icon.iconfont &#xe693;
                        div.btn-follow(v-else @click="follow(item)")
                            i.icon.iconfont &#xe692;
            <div v-if="allLoaded" class="no-more-data">无更多数据</div>
        null-data(v-else)
</template>
<script>
import { Toast } from 'mint-ui'
import nullData from '@/components/nullData'
export default {
    props: ['list', 'allLoaded'],
    components: {
        nullData
    },
    methods: {
        follow(item) {
            this.axios.get(this.api.userFollow(), {
                params: {
                    to_user_id: item.user_id
                }
            })
                .then(res => {
                    if (res.data.status == 1) {
                        (item.follow_id != undefined) && (item.follow_id = res.data.has_focus);
                        (item.has_focus != undefined) && (item.has_focus = res.data.has_focus);
                    }
                    else {
                        Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    Toast(err || '请求失败');
                });
        }
    },
    computed: {
        iconLevel() {
            let arr_user_level = [];
            for (let val of this.list) {
                arr_user_level.push(require(`@/assets/img/rank/rank_${val.user_level}.png`));
            }
            return arr_user_level;
        }
    }
}
</script>
<style lang="less" scoped>
@import "../assets/css/variable.less";
.user-list {
    ul {
        background-color: #fff;
        padding-left: .5rem;
        .list {
            height: 3.25rem;
            width: 100%;
            display: flex;
            align-items: center;
            position: relative;
            .head-img {
                height: 2rem;
                width: 2rem;
                border-radius: 2.5rem;
                overflow: hidden;
                flex-shrink: 0;
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .user-information {
                border-bottom: 1px solid #e6e8f1;
                width: 100%;
                height: 3.25rem;
                padding: 0 .5rem;
                font-size: .65rem;
                display: flex;
                justify-content: space-between;

                .user-title {
                    padding-top: .75rem;
                    width: 65%;
                    .user-nick-name {
                        display: flex;
                        align-items: center;
                        span {
                            font-size: .75rem;
                            color: @fc-main;
                            display: block;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                        }
                        .praise {
                            .iconfont {
                                font-size: .75rem;
                                margin: 0 .25rem;
                                line-height: 1;
                            }
                            .sex-woman {
                                color: #ff71bb;
                            }
                            .sex-man {
                                color: #3fa2ff;
                            }
                            img {
                                width: 1.3rem;
                                height: .65rem;
                                vertical-align: text-top;
                            }
                        }
                    }
                    .signature {
                        font-size: .7rem;
                        color: @fc-light;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                }
                .after {
                    position: absolute;
                    right: .5rem;
                    top: 50%;
                    margin-top: -.5rem;
                    display: flex;
                    align-items: center;
                    .btn-follow {
                        height: 1rem;
                        line-height: 1rem;
                        width: 1.75rem;
                        background: @color-theme;
                        border-radius: .5rem;
                        text-align: center;
                        .iconfont {
                            color: #fff;
                            vertical-align: middle;
                        }
                        &.followed {
                            background: @color-disabled;
                        }
                    }
                }
            }
        }
    }
}
</style>




