<template lang="pug">
    div.user-fans-content
        mt-loadmore(
            :top-method="loadTop"
            :bottom-method="loadBottom"
            :bottom-all-loaded="allLoaded"
            :autoFill="autoFill"
            ref="loadmore"
            @top-status-change="handleTopChange"
            @bottom-status-change="handleBottomChange"
        )
            mint-loadmore-top(:topStatus="topStatus" slot="top")
            template(v-if="isLoaded")
                user-list(:list="list" :allLoaded="allLoaded")
            mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="!allLoaded")
</template>
<script>
import { Toast } from 'mint-ui'
import userList from '@/components/userList'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
export default {
    name: 'toolFollow',
    components: {
        userList,
        mintLoadmoreTop,
        mintLoadmoreBottom
    },
    data() {
        return {
            list: '',
            has_next: '',
            page: 2,
            autoFill: false,
            allLoaded: false,
            isLoaded: false,
            topStatus: '',
            bottomStatus: ''
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData(isRefresh = false) {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/userUser_follow' : this.api.userUser_follow();
            this.axios.get(axiosUrl, {
                params: {
                    to_user_id: this.$route.query.to_user_id
                }
            }).then(res => {
                this.isLoaded = true;
                if (res.data.status == 1) {
                    this.list = res.data.list;
                    this.has_next = res.data.has_next;
                    this.page = 2;
                }
                else {
                    Toast(res.data.error || '操作失败');
                }
                this.$nextTick(function() {
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                });
            })
                .catch(err => {
                    Toast(err || '网络异常，请求失败');
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                });
        },
        loadTop(id) {
            // 下拉刷新加载
            this.getData(true);
        },
        loadBottom() {
            // 无限加载
            let self = this;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/userUser_follow' : self.api.userUser_follow();
            if (self.has_next) {
                self.axios.get(axiosUrl, {
                    params: {
                        p: self.page,
                        to_user_id: self.$route.query.to_user_id
                    }
                }).then(res => {
                    setTimeout(() => {
                        if (res.data.status == 1) {

                            self.has_next = res.data.has_next;
                            self.page = self.page + 1;

                            if (res.data.list && res.data.list.length && res.data.list.length > 0) {
                                for (let i = 0; i < res.data.list.length; i++) {
                                    self.list.push(res.data.list[i]);
                                }
                            }
                        }
                        else {
                            Toast(res.data.error || '操作失败');
                        }

                        self.$nextTick(function() {
                            self.$refs.loadmore.onBottomLoaded();
                        });
                    }, 500);
                })
                    .catch(err => {
                        Toast(err || '网络异常，请求失败');
                        self.$refs.loadmore.onBottomLoaded();
                    });
            }
            else {
                return false;
            }
        },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        },
        handleBottomChange(status) {
            // 获取上拉最新状态
            this.bottomStatus = status;
        }
    },
    watch: {
        list() {
            // 当页面数据发生改变,进行处理
            this.allLoaded = !Boolean(this.has_next);

        }
    }
}
</script>



